import axios from "axios";

// 查看个人信息
const getPersonalInfoApi = () => new Promise((resolve, reject) =>{
    axios({
        method:'get',
        url:'/api/basicInfo',
        headers:{
            "Content-Type": "application/json",
        },
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

// 修改密码
const getUpdatePasswordApi = (oldPassword, newPassword) => new Promise((resolve, reject) =>{
    let data = {
        oldPassword: oldPassword,
        newPassword: newPassword,
    };
    axios({
        method:'post',
        url:'/api/update/password',
        headers:{
            "Content-Type": "application/json",
        },
    data: JSON.stringify(data),
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

// 修改个人信息(管理员)
const getEditPersonalInfoApi = (personalInfo, hireDateNew) => new Promise((resolve, reject) =>{
    let data = {
        id: personalInfo.id,
        name: personalInfo.name,
        username: personalInfo.username,
        workNum: personalInfo.workNum,
        department: personalInfo.department,
        supervisor1: personalInfo.supervisor1,
        supervisor2: personalInfo.supervisor2,
        supervisor3: personalInfo.supervisor3,
        hr: personalInfo.hr,
        superAdmin: personalInfo.superAdmin,
        firstAdmin: personalInfo.firstAdmin,
        secondAdmin: personalInfo.secondAdmin,
        // supervisorName1: personalInfo.supervisorName1,
        // supervisorName2: personalInfo.supervisorName2,
        // supervisorName3: personalInfo.supervisorName3,
        // supervisorName4: personalInfo.supervisorName4,
        hireDate: hireDateNew,
        residence: personalInfo.residence,
        phoneNumber: personalInfo.phoneNumber,
        email: personalInfo.email,
        businessType: personalInfo.businessType,
        lxyz: personalInfo.lxyz,
        role: personalInfo.role,
        password: personalInfo.password
    };
    axios({
        method:'post',
        url:'/api/update/selfInfo',
        headers:{
            "Content-Type": "application/json",
        },
    data: JSON.stringify(data),
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

