<!-- 修改个人信息通用组件 -->
<template>
    <div class="container">
        <div class="password-edit">
            <div class="password-header">
                <span class="title">修改密码</span>
                <a-button type="primary" ghost class="right-align" @click="handleResetPassword">修改</a-button>
            </div>
            <div class="password-form">
                <a-form>
                    <a-row :gutter="72">
                        <a-col :span="8">
                            <a-form-item label="旧密码">
                                <a-input :disabled="passwordIsEdit" placeholder="请输入旧密码"
                                    v-model:value="formState.oldPassword"></a-input>
                            </a-form-item>
                        </a-col>
                        <a-col :span="8">
                            <a-form-item label="新密码">
                                <a-input :disabled="passwordIsEdit" placeholder="请输入新密码"
                                    v-model:value="formState.newPassword1"></a-input>
                            </a-form-item>
                        </a-col>
                        <a-col :span="8">
                            <a-form-item label="确认密码">
                                <a-input :disabled="passwordIsEdit" placeholder="请确认新密码"
                                    v-model:value="formState.newPassword2"></a-input>
                            </a-form-item>
                        </a-col>
                    </a-row>
                    <a-form-item>
                        <a-button v-if="!passwordIsEdit" type="primary" class="center-button"
                            @click="handleSubmitPassword">提交</a-button>
                    </a-form-item>
                </a-form>
            </div>
        </div>
        <div class="personal-info-edit">
            <div class="info-header">
                <span class="title">维护个人信息</span>
                <a-button type="primary" ghost class="right-align" @click="handleResetInfo">修改</a-button>
            </div>
            <div class="personal-info-form">
                <a-form>
                    <a-row :gutter="72">
                        <a-col :span="8">
                            <a-form-item label="姓名">
                                <a-input :disabled="infoIsEdit" placeholder="请输入姓名"
                                    v-model:value="userinfo.name"></a-input>
                            </a-form-item>
                        </a-col>
                        <a-col :span="8">
                            <a-form-item label="工号">
                                <a-input :disabled="infoIsEdit" placeholder="请输入工号"
                                    v-model:value="userinfo.jobNumber"></a-input>
                            </a-form-item>
                        </a-col>
                        <a-col :span="8">
                            <a-form-item label="部门">
                                    <a-select :disabled="infoIsEdit" placeholder="请输入部门" v-model:value="userinfo.department">
                                        <a-select-option v-for="item in departmentList" :key="item" :value="item">{{ item
                                }}</a-select-option>
                                    </a-select>
                            </a-form-item>
                        </a-col>
                    </a-row>

                    <a-row :gutter="72">
                        <a-col :span="8">
                            <a-form-item label="手机号">
                                <a-input :disabled="infoIsEdit" placeholder="请输入手机号"
                                    v-model:value="userinfo.phone"></a-input>
                            </a-form-item>
                        </a-col>
                        <a-col :span="8">
                            <a-form-item label="邮箱">
                                <a-input :disabled="infoIsEdit" placeholder="请输入邮箱"
                                    v-model:value="userinfo.email"></a-input>
                            </a-form-item>
                        </a-col>
                        <a-col :span="8">
                            <a-form-item label="入职时间">
                                <a-date-picker style="width: 100%;" :disabled="infoIsEdit" placeholder="请选择入职时间"
                                    v-model:value="userinfo.entry_time" />
                            </a-form-item>
                        </a-col>
                    </a-row>

                    <a-row :gutter="72">
                        <a-col :span="8">
                            <a-form-item label="主管1">
                                <a-input placeholder="" v-model:value="userinfo.manager1" disabled></a-input>
                            </a-form-item>
                        </a-col>
                        <a-col :span="8">
                            <a-form-item label="主管2">
                                <a-input placeholder="" v-model:value="userinfo.manager2" disabled></a-input>
                            </a-form-item>
                        </a-col>
                        <a-col :span="8">
                            <a-form-item label="主管3">
                                <a-input placeholder="" v-model:value="userinfo.manager3" disabled></a-input>
                            </a-form-item>
                        </a-col>
                    </a-row>

                    <a-row :gutter="72">
                        <a-col :span="8">
                            <a-form-item label="HRBP">
                                <a-input placeholder="" v-model:value="userinfo.hrbp" disabled></a-input>
                            </a-form-item>
                        </a-col>
                        <a-col :span="8">
                            <a-form-item label="系统角色">
                                <a-input placeholder="请选择系统角色" v-model:value="userinfo.role" disabled></a-input>
                            </a-form-item>
                        </a-col>
                    </a-row>
                    <a-form-item>
                        <a-button v-if="!infoIsEdit" type="primary" class="center-button"
                            @click="handleSubmitInfo">提交</a-button>
                    </a-form-item>
                </a-form>
            </div>
        </div>
    </div>
</template>

<script setup>
import { message } from "ant-design-vue";
import { onMounted, ref } from "vue"
import dayjs from 'dayjs';
import { getPersonalInfoApi, getUpdatePasswordApi, getEditPersonalInfoPersonApi } from '@/api/personalInfo'
import { getDepartmentListApi } from "@/api/userManage";
import { useRouter } from 'vue-router';
import { logoutApi } from '@/api/login'

onMounted(() => {
    getPersonalInfo(),
    getDepartmentList()
})

const router = useRouter();
const formState = ref({
    oldPassword: "",
    newPassword1: "",
    newPassword2: ""
})
const userinfo = ref({
    name: "",
    jobNumber: "",
    department: "",
    phone: "",
    email: "",
    entry_time: dayjs("2023-01-01"),  // 注意：这里要使用dayjs格式化后才可正常回显
    supervisor1:"",
    supervisor2:"",
    supervisor3:"",
    hr:"",
    manager1: "",
    manager2: "",
    manager3: "",
    hrbp: "",
    role: ""
})
const passwordIsEdit = ref(true)
const handleResetPassword = () => {
    passwordIsEdit.value = !passwordIsEdit.value
}

// 获取部门列表
const departmentList = ref(['部门a', '部门b'])
const getDepartmentList = () => {
    getDepartmentListApi().then(res => {
        if (res.data.code === 200) {
            departmentList.value = []
            for (let i = 0; i < res.data.data.length; i++) {
                departmentList.value.push(res.data.data[i])
            }
        } else {
            message.error('获取失败！', res.data.msg)
        }
    }).catch(err => {
        message.error('请求失败！', err)
    })
}

// 调用api-获取个人信息
const getPersonalInfo = () => {
    getPersonalInfoApi().then(res => {
        if (res.data.code === 200) {
            // 1、将获取到的个人信息赋值给userinfo
            userinfo.value.id = res.data.data.id
            userinfo.value.name = res.data.data.name
            userinfo.value.username = res.data.data.username
            userinfo.value.jobNumber = res.data.data.workNum
            userinfo.value.department = res.data.data.department
            userinfo.value.phone = res.data.data.phoneNumber
            userinfo.value.email = res.data.data.email
            userinfo.value.entry_time = dayjs(res.data.data.hireDate),
            userinfo.value.supervisor1 = res.data.data.supervisor1
            userinfo.value.supervisor2 = res.data.data.supervisor2
            userinfo.value.supervisor3 = res.data.data.supervisor3
            userinfo.value.hr = res.data.data.hr
            userinfo.value.manager1 = res.data.data.supervisorName1
            userinfo.value.manager2 = res.data.data.supervisorName2
            userinfo.value.manager3 = res.data.data.supervisorName3
            userinfo.value.hrbp = res.data.data.hrName
            userinfo.value.role = res.data.data.role

            // 2、映射 role 到对应的角色名称
            const roleMap = {
                0: '普通用户',
                1: '一级管理员',
                2: '二级管理员',
                3: '超级管理员'
            };
            userinfo.value.role = roleMap[res.data.data.role] || '未知角色'; // 防止未知角色值
        } else {
            message.error("获取个人信息失败！" + res.data.msg);
        }
    }).catch(err => {
        message.error("请求失败！" + err);
    })
}
// 调用api-修改密码
const handleSubmitPassword = () => {
    // 1、验证两次密码不为空，且输入是否一致
    if (formState.value.oldPassword === "" || formState.value.newPassword1 === "" || formState.value.newPassword2 === "") {
        message.warning("请输入密码！");
        return
    } else if (formState.value.newPassword1 !== formState.value.newPassword2) {
        message.error("两次新密码输入不一致！");
        return
    } else {
        // 2、调用api-修改密码（传入接口：旧密码、新密码）
        getUpdatePasswordApi(formState.value.oldPassword, formState.value.newPassword1).then(res => {
            if (res.data.code === 200) {
                message.success("修改密码成功！请重新登录~")
                // 3、将输入框变为不可编辑
                passwordIsEdit.value = true
                // 4、清空输入框
                formState.value.oldPassword = ""
                formState.value.newPassword1 = ""
                formState.value.newPassword2 = ""
                // 5、返回登录页面
                logout()
            } else {
                message.error("修改失败！" + res.data.msg);
            }
        }).catch(err => {
            message.error("请求失败！" + err);
        })
    }
}
const infoIsEdit = ref(true)
const handleResetInfo = () => {
    infoIsEdit.value = !infoIsEdit.value
}
// 调用api-修改个人信息
const handleSubmitInfo = () => {
    // 1、获取输入框中的个人信息
    // 若userinfo中的字段存在空值，则提示用户输入完整信息
    if (userinfo.value.name === "" || userinfo.value.email === "" || userinfo.value.id === "" || userinfo.value.entry_time === "" || userinfo.value.department === "" || userinfo.value.manager === "" || userinfo.value.phone === "" || userinfo.value.role === "") {
        message.warning("请输入完整信息！");
    } else {
        // 2、调用api-修改个人信息
        const personalInfo = userinfo.value
        // 处理入职时间格式
        const hireDate = personalInfo.entry_time.$d
        const year = hireDate.getFullYear();
        const month = String(hireDate.getMonth() + 1).padStart(2, '0'); // 月份从0开始，需要加1
        const day = String(hireDate.getDate()).padStart(2, '0'); // 确保天数是两位数
        const hireDateNew = `${year}-${month}-${day}`
        // 调用api-修改信息
        getEditPersonalInfoPersonApi(personalInfo, hireDateNew).then(res => {
            if (res.data.code === 200) {
                message.success('修改成功！^_^')
                getPersonalInfo()
                infoIsEdit.value = true
            } else {
                message.error('修改失败！', res.data.msg)
            }
        }).catch(err => {
            console.log(err)
        })        
    }

}

// 退出登录
const logout = () => {
    logoutApi().then(res => {
        if(res.data.code === 200){
            router.push({ path: '/' })
            message.success('您已退出登录！');
        }
    }).catch(err => {
        if(err.data.code === 403){
            router.push({ path: '/' })
            message.error('登录信息已失效，请重新登录！');
        }else {
            console.log('退出登录失败！', err)
        }
    })
}
</script>

<style lang="less" scoped>
