    display: flex;
    flex-direction: column;
    height: 100%;
}

.password-edit {
    flex: 1;
    padding: 0px 15px 15px 15px;
}

.personal-info-edit {
    flex: 4;
    padding: 5px 15px 15px 15px;
}

