import axios from "axios";

//获取用户表
const getUserInfoListApi = (pageNum, pageSize, department, role, username) => new Promise((resolve, reject) =>{
    let data = {
        pageNum: pageNum,
        pageSize: pageSize,
        department: department,
        role: role,
        username: username
    };
    axios({
        method:'post',
        url:'/api/basicInfos',
        headers:{
            "Content-Type": "application/json",
        },
        data: JSON.stringify(data),
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

//获取部门列表
const getDepartmentListApi = () => new Promise((resolve, reject) =>{
    axios({
        method:'post',
        url:'/api/departments',
        headers:{
            "Content-Type": "application/json",
        },
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

// 删除用户（单个）
const getDeleteUserApi = (id) => new Promise((resolve, reject) =>{
    axios({
        method:'delete',
        url:'/api/delete/user',
        headers:{
            "Content-Type": "application/json",
        },
        params: {
            id: id
        }
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

// 上传用户信息文件
const getUserInfoExcelApi = (file) => new Promise((resolve, reject) => {
    // 创建 FormData 对象
    let formData = new FormData();
    
    // 将文件添加到 FormData 对象
    formData.append('file', file);

    // 发起 POST 请求
    axios({
        method: 'post',
        url: '/api/upload',
        headers: {
            'Content-Type': 'multipart/form-data',
        },
        data: formData,
    })
    .then((response) => {
        // 请求成功的处理
        resolve(response);
    })
    .catch((error) => {
        // 请求失败的处理
        reject(error.response);
    });
});

// 删除用户（批量）
const getDeleteUserManyApi = (ids) => new Promise((resolve, reject) =>{
    let data = {
        ids: ids  // 示例数据：[155, 156, 157, 158]
    }
    axios({
        method:'delete',
        url:'/api/delete/users',
        headers:{
            "Content-Type": "application/json",
        },
        data: JSON.stringify(data),
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})


export{
    getUserInfoListApi,
    getDepartmentListApi,
    getDeleteUserApi,
    getUserInfoExcelApi,
    getDeleteUserManyApi
}